	module.exports = {"data":{"post":{"html":"<p>If you are in charge of tourism for your town, you probably don't have to have <a href=\"http://english.peopledaily.com.cn/leaders/jzm/jzmhome.htm\">Chinese President Jiang Zemin</a> (yes, the <a href=\"http://english.peopledaily.com.cn/\">People's Daily</a> has a home page for him) drop in for a visit. He's passing through the VW plant in Wolfsburg and the historic town of Goslar near Hannover today. NDR4 Radio reported yester on the security precautions in Goslar. The town center will be sealed off. Police will be posted in all entryways. Noone can enter or leave any building.  Noone is allowed  is allowed within a 15 m \"stone's throw\" buffer around the President.  No, today is not a good day for normal tourists to visit Goslar. Like the  <a href=\"http://www.faz.com/IN/INtemplates/eFAZ/docmain.asp?rub={B1311FCC-FBFB-11D2-B228-00105A9CAF88}&amp;doc={DB7EE0FB-9EFD-4EB6-B1F5-E1CC6F42D670}\">FAZ concludes</a>, all Jiang wants is a spring time photo op. </p>\n<p>And stories at home like this: <a href=\"http://english.peopledaily.com.cn/200204/10/eng20020410_93830.shtml\">People's Daily: Jiang Calls for Closer China-Germany Cooperation</a></p>","fields":{"title":"Germany in the Spring and Jiang Zemin","date":"12 Apr 2002","url":"https://papascott.de/2002-04-12-germany-in-the-spring-and-jiang-zemin/"}}},"pathContext":{"slug":"/2002-04-12-germany-in-the-spring-and-jiang-zemin/","prev":{"fields":{"title":"Explaining the Unexplainable","date":"11 Apr 2002","slug":"/2002-04-11-explaining-the-unexplainable/"}},"next":{"fields":{"title":"Komodo","date":"15 Apr 2002","slug":"/2002-04-15-komodo/"}}}}

